import IconButton from '@material-ui/core/IconButton'
import Paper from '@material-ui/core/Paper'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import PencilIcon from '@material-ui/icons/Edit'
import HamburgerIcon from '@material-ui/icons/CalendarViewDay'
import DeleteIcon from '@material-ui/icons/Delete'
import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { fetchSuppliers } from '../SuppliersActions'
import Paginate from '../../main/pages/Paginate'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

import consts from '../../../consts'

class List extends Component {

  constructor(props){
    super(props)

    this.state = {
        rowsPerPage: consts.DEFAULT_PAGINATION,
    }
  }

  handleChangeRowsPerPage = event => {
    this.setState({rowsPerPage: event.target.value}, () => {
      this.onMovePage(null, null, 0);
    })
  }

  onMovePage = (event, newPage) => {
    this.props.fetchSuppliers(newPage + 1, this.props.filters, this.state.rowsPerPage).then(data => {
    })
  }

  render() {
    const { classes, items } = this.props
    return (
      <Paper className={classes.content} elevation={1}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Identificador</TableCell>
              <TableCell>Description</TableCell>
              <TableCell>CNPJ</TableCell>
              <TableCell>Ações</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {items && items.data && items.data.map(item => (
              <TableRow key={item.id}>
                <TableCell>{item.id}</TableCell>
                <TableCell>{item.description}</TableCell>
                <TableCell>{item.CNPJ}</TableCell>
                <TableCell>
                  <IconButton
                    size="small"
                    component={Link}
                    to={`/suppliers/${item.id}/edit`}
                  >
                    <PencilIcon />
                  </IconButton>
                  <IconButton
                    size="small"
                    component={Link}
                    to={`/suppliers/${item.id}/delete`}
                  >
                    <DeleteIcon />
                  </IconButton>

                  <IconButton
                    size="small"
                    component={Link}
                    to={`/suppliers/${item.id}/list-centers`}
                  >
                    <HamburgerIcon />
                  </IconButton>

                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <Paginate handleChangeRowsPerPage={this.handleChangeRowsPerPage} rowsPerPage={this.state.rowsPerPage} rows={items.total} page={items.current_page -1} handleChangePage={this.onMovePage} />
      </Paper>
    )
  }
}

List.propTypes = {
  classes: PropTypes.object.isRequired,
  items: PropTypes.object.isRequired,
}

List.defaultProps = {
  items: [],
}

const mapStateToProps = state => ({
  filters: state.supplier.filters 
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      fetchSuppliers,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(List)

